.body {
  margin: 0;
  padding: 0;
}

#root {
  margin: 0;
  padding: 0;
  
  min-height: 100vh;
}

.main {
    font-family: Arial, Helvetica, sans-serif;
    margin: 0px;
    max-width: 100%;
    background-color: #CEF6CE;
    padding: 0;
    min-height: 100vh;
    border: 20px solid #CEF6CE;
    box-sizing: border-box;
    }

  .header {
    background-color:#BCA9F5;
    height: 3rem;
    align-content: center;
    width: 100%;
  }


  .header__wrap {
    margin-left: auto;
    width: 65%;
    display: flex;
    justify-content: space-between;
    padding: 5px;
    align-items: center;
  }

  .header__title {
    color: #FFFFFF;
    font-size: 2rem;
    font-weight: 400;
    margin: 0;
    padding: 0;
  }

  .header__title.header__title_av {
    color: #F6CEEC;
    font-size: 2rem;
    font-weight: 700;
  }
  
  .header__counter {
    border-radius: 20%;
    padding: 8px;
    width: 32px;
    height: 32px;
    
    background-color: #F2F2F2;
    text-align: center;
    box-sizing: border-box;
  }
 
  .header__number {
    color: #151515;
    font-size: 1rem;
    font-weight: 700;
    margin: 0;
    vertical-align: middle;
  }

  .container {
    min-height: calc(100vh - 3rem - 15px);
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-top: 15px;
  }

  .aside {
    width: 30%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: #CEF6CE;
  }

  .form {
    box-sizing: border-box;
    padding: 15px 20px;
    min-height: 11%;
    background-color:#BCA9F5;
    display: flex;
    flex-direction: column;
  }

  .form__buttons {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
  }

  .form__draft-btn {
    width: 40%;
    background-color: #BDBDBD;
  }

  .form__save-btn {
    width: 50%;
    background-color: #58FA82;
    
  }

  .form__input {
    padding: 5px 10px;
  }

  .button {
    color: #FFFFFF;
    border: none;
    padding: 0;
    font-size: 1rem;
    padding: 5px;
    cursor: pointer;
  }

  .draft {
    min-height: 86%;
    display: flex;
    flex-direction: column;
    padding: 10px 15px;
    background-color:#BCA9F5;
    padding: 15px 20px;
   box-sizing: border-box;
   margin-top: 15px;
  }


  .draft__item { 
    margin-bottom: 35px;
    position: relative;
    background-color: #ebe6e6;
  }

  .draft__title {
    color: #FFFFFF;
    font-size: 2rem;
    font-weight: 500;
    margin: 25px;
    padding: 0;
  }

  .draft__card-text {
    width: 100%;
    outline: none;
    resize: none;
    background-color: #ebe6e6;
    border: none;
    padding: 10px 10px;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
  }

  .draft__buttons {
    position: absolute;
    bottom: -1rem;
    right: 1rem;
    display: flex;
    width: 50%;
    /*margin: -1rem 10px 0 auto;*/
    justify-content: space-between;
  }

  .draft__remove-btn {
    background-color: #FF0000;
  }

  .draft__save-btn {
    background-color: #58FA82;
  }

  .cards__section {
    min-height: 100%;
    padding: 15px 30px;
    box-sizing: border-box;
    align-content: center; 
    background-color:#BCA9F5;
    width: 68%;
  }
  
  .cards__list {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-start;
  }

  .card__item {
    background-color: #faeded;
    padding: 10px;
    width: 10rem;
    height: 10rem;
    margin-bottom: 15px;
    position: relative;
  }

  .card__header {
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
  }

  .card__text {
    width: 80%;
    font-size: 1.5em;
    font-weight: 400;
    color: #000000;
    padding: 0;
    margin: 0;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
  }

  .card__indicator {
    width: 20px;
    height: 20px;
    
  }

  .red {
    background-color: #EE3E3E;
  }

  .green {
    background-color: #04B404;
  }

  .list {
    list-style-type: none;
    /*margin: 0;*/
    padding: 0;
  }

  input::placeholder {
    color: #000000;
  }

  textarea::placeholder {
    color: #000000;
  }
 
  .card__footer {
    position: absolute;
    left: 10px;
    bottom: 10px;
    display: flex;
    justify-content: space-between;
    width: 10rem;
    
  }

  .card__btn-draft {
    background-color: #ad9595;
    padding: 7px;
    box-sizing: border-box;
    width: 30%;
  }

  .card__btn-mark {
    background-color: #ad9595;
    padding:7px;
    box-sizing: border-box;
    width: 50%;
  }


  